@import url(https://fonts.googleapis.com/earlyaccess/cwtexkai.css);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
  font-family: 'cwTeXKai', 'Fira Sans';
  font-weight: 600;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.navbar {
    background-image: url(/Web_final/static/media/banner-collaborate.91b383d6.jpg);
    background-size: cover;
    background-position: 25% 75%;
    height: 200px;
    display: flex;
    justify-content: flex-end;
    align-items: center;
}

.navbar-text {
    text-decoration: none;
    color: #3A5D99;
    font-size: 72px;
}

.navbar-menu {
    margin-top: 4em;
    margin-right: 2rem;
    font-size: 2rem;
    background: none;
    justify-content: flex-end;
}

.nav-menu {
    background-color: #83B9C7;
    margin-top: 200px;
    width: 20%;
    height: 100vh;
    display: flex;
    justify-content: center;
    position: fixed;
    top: 0;
    right: -100%;
    transition: 850ms;
}

.nav-menu.active {
    right: 0;
    transition: 350ms;
}

.nav-text {
    display: flex;
    justify-content: flex-start;
    align-items: center;
    padding: 8px 0px 8px 8px;
    list-style: none;
    height: 60px;
}

.nav-text a {
    text-decoration: none;
    font-family: cursive;
    color: #3A5D99;
    font-size: 18px;
    width: 95%;
    height: 100%;
    display: flex;
    align-items: center;
    padding: 0 16px;
    border-radius: 4px;
}

.nav-text a:hover {
    background-color: #1a83ff;
}

.nav-menu-items {
    width: 100%;
}

span {
    margin-left: 16px;
}
.home-header {
    position: relative;
    background-color: #2056ac;
    color: azure;
    height: 10vh;
    font-size: 50px;
    font-family: cursive;
    padding: 30px;
}

.home-button {
    position: absolute;
    height: 40px;
    width: 50px;
    right: 20px;
}
* {
    box-sizing: border-box;
}

.home {
    height: 100vh;
}

.place_Announce {
    width: 80%;
    float: left;
    background-color: #FFF8DC;
}

.Announce_button {
    float: left;
    margin-left: 10px;
    margin-top: 13px;
    height: 30px;
    font-size: 15px;
    border-radius: 10px;
    color: white;
    background-color: green;
    cursor: pointer;
}

.Announce_button:first-child {
    margin-left: 45px;
    background-color: red;
}

.place_Weblink {
    width: 20%;
    float: right;
    background-color: #AFEEEE;
    height: 100%;
}

.place_Weblink h1 {
    margin-top: 1em;
    font-size: 2.7em;
    color: #2F4F4F;
    font-weight: 1000;
}

.place_Weblink h2 {
    margin:auto;
    width: 40%;
    font-size: 1.5em;
    font-weight: 700;
    background-color: rgb(61, 228, 172);
    border-radius: 10px;
    color: rgb(114, 70, 16);
    cursor: pointer;
}

.WebPage_h1 {
    margin-top: 5px;
    margin-bottom: 5px;
    margin-right: 10vw;
}

.WebPage_ul {
    list-style: none;
    width: 100%;
}

.WebPage_li {
    display: flex;
    flex-direction: row;
    width: 100%;
    height: 6vh;
    border-top: 1px solid #666;
}

.WebPage_li h2 {
    border-radius: 10px;
    margin-top: 1.5vh;
    padding: 0 6px;
    height: 30px;
    font-size: 1em;
    color: #fff;
    line-height: 30px;
    float: left;
}

.WebPage_li a {
    margin-left: 0.5rem !important;
    margin-top: 1.5vh;
    font-size: 1.3em;
    float: left;
    overflow : hidden;
    text-overflow : ellipsis;
    color: #556B2F;
    width: 60vw;
    text-align: left;
    padding-left: 1vw;
}

.WebPage_li h3 {
    float: right;
    font-size: 1.2em;
    margin-top: 1.5vh;
    margin-right: 1.2vw;
    margin-bottom: 0;
    color: #191970;
    margin-left: 5.7vw;
}

.Weblink_ul {
    margin-top: 2em;
    width: 100%;
}

.Weblink_li {
    width: 100%;
    height: 50px;
}

.Weblink_a {
    margin-left: 0.5rem !important;
    font-size: 1.8em;
    font-weight: 600;
    float: left;
    padding-top: 0px;
    overflow : hidden;
    text-overflow : ellipsis;
    color: #800000;
}

.Weblink-x {
    display: none;
}

.Weblink_li:hover .Weblink-x {
    display: inline;
    position: absolute;
    margin-top: 5px;
    max-width: 20px;
    max-height: 20px;
    right: 20px;
}

.Weblink_li:hover .Weblink-x:hover {
    margin-top: 5px;
    max-width: 25px;
    max-height: 25px;
    right: 17.5px;
}

.Add_link_li {
    float: left;
    width: 100%;
    list-style-type: none;
}

.Add_link_li h1 {
    float: left;
    padding-right: 10vw;
    font-size: 1.5rem;
}

.Add_link_button {
    margin-top: 5vh;
    margin-right: 2vw;
    float: right;
}
.calendar {
    width: 60vw;
    margin-left: 5vw;
    float: left;
}

.right {
    width: 25vw;
    margin-right: 5vw;
    float: right;
}

* {
    box-sizing: border-box;
}

.todo-app__list {
    list-style: none;
    padding: 0;
    /* 5*5=25 */
}

.todo-list-h1 {
    margin-top: 1vh;
}

.todo-app__item {
    width: 100%;
    min-height: 5em;
    display: flex;
    flex-direction: row;
    align-items: center;
    background: rgb(249, 252, 215);
    border: 1px solid rgba(0, 0, 0, 0.089);
}

.todo-app__item-date {
    font-size: 1.5em;
    font-weight: 550;
    overflow-wrap: break-word;
    transition: opacity 0.3s;
    height: 100%;
    margin-bottom: 0vh;
    margin-left: 1vw;
    text-align: left;
    width: 30%;
    float: left;
}

.todo-app__item-detail {
    font-size: 1.5em;
    font-weight: 550;
    overflow-wrap: break-word;
    transition: opacity 0.3s;
    height: 100%;
    margin-bottom: 0vh;
    margin-left: 1vw;
    text-align: left;
    width: 45%;
    float: left;
}

.todo-app__item-x {
    display: none;
}

.todo-app__item:hover .todo-app__item-x {
    display: inline;
    float: right;
    position: relative;
    max-width: 2vw;
    max-height: 2vh;
    margin-right: 2vw;
    margin-left: 1vw;
}

.todo-app__item:hover .todo-app__item-x:hover {
    max-width: 3vw;
    max-height: 3vh;
    margin-right: 1vw;
}
.placeright {
    width: 25vw;
    margin-right: 3vw;
    float: right;
}

* {
    box-sizing: border-box;
}

.create_task__list {
    list-style: none;
    padding: 1px;
    margin-top: 8vh;
    /* 5*5=25 */
}

.create_item__item {
    position: flex;
    width: 100%;
    min-height: 5em;
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    align-items: center;
    background: rgb(241, 210, 240);
    border: 1px solid rgba(0, 0, 0, 0.089);
}

.create_item-detail {
    margin-bottom: 0vh;
    margin-left: 2vw;
    text-align: left;
    font-weight: 6000;
    font-size: 25px;
    overflow-wrap: break-word;
    width: 90%;
    transition: opacity 0.3s;
    height: 50%;
}

.create_item-input {
    margin-right: 1vw;
    background-color: cornsilk;
}

.create_item-checkbox {
    margin-right: 6vw; /* IE */ /* FF */ /* Safari and Chrome */ /* Opera */
    transform: scale(2);
}

.create_item_btn {
    margin-top: 2vh;
    margin-right: 1vw;
    margin-left: 1vw;
}
